import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"
import bann02 from "../images/bann2.png"

export default function Clean1() {
    const data = useStaticQuery(graphql`
query Clean1 {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section className="mision-area">
            <div className="div-tittle-mision">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <p>Visión</p>
                <div className="div-text-mision">
                <div className="grid-container-valores">
                        <div>
                        <button className="label-listvalores">-1-</button>
                        <div className="p-listvalores">
                        <p><b>Calidad:</b> Nos gusta estar siempre comprometidos con procesos de mejora continua en los cuales todas las áreas 
                            de la empresa participen activamente en el desarrollo de productos y servicios que puedan satisfacer las 
                            expectativas de nuestros Clientes</p>
                        </div>
                        </div>
                        <p><b>Transparencia:</b> Somos respetuosas con las normas y leyes establecidas que implican 
                            procedimientos honrados y totalmente claros que garantizan la legalidad de los mismos.</p>
                        <p><b>Honestidad:</b> Nos gusta y creemos en la honestidad como uno de los pilares fundamentales 
                            que rigen todas las actividades de cualquier empresa y nos comportamos y expresamos con 
                            coherencia y sinceridad de acuerdo con los valores de verdad y justicia.</p>
                        <p><b>Compromiso:</b> Destacamos el valor del compromiso de cada integrante de la empresa 
                            con sus responsabilidades asumidas, creemos en el trabajo de cada uno y en su valor dentro de 
                            la empresa.</p>
                        <p><b>Innovación:</b> Nos encanta innovar, buscar siempre procesos constantes de desarrollo
                            y poder implementar nuevas ideas en productos o servicios que encajen con la estrategia 
                            de la empresa y aporten valor añadido a nuestros clientes.</p>
                        <p><b>Rentabilidad:</b> Sabemos que la rentabilidad es una responsabilidad primordial en cualquier 
                            empresa, por lo que gestionamos de manera efectiva los recursos para obtener resultados 
                            económicos que beneficien a Star Clean y a todos sus miembros, garantizando la permanencia de
                            la misma.</p>
                        <p><b>Respeto:</b> Nos encanta fomentar la capacidad de aceptar diferentes criterios y actitudes 
                            dentro de la filosofía de nuestra empresa.</p>
                        <p><b>Trabajo en equipo:</b> Sabemos que somos un grupo de personas con diferentes capacidades 
                            complementarias, comprometidas con un propósito y un objetivo de trabajo, guiadas por un 
                            planeamiento común con responsabilidades compartidas.</p>
                    </div>
                </div>
            </div>
            <img className="mision-banner" alt="" src={bann02} />
        </section>
    )
}