import Img from "gatsby-image"
import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import "../css/style.css"
import bann01 from "../images/bann1.png"

export default function Valores() {
    const data = useStaticQuery(graphql`
query Valores {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section className="valores-area">
            <div className="div-tittle-valores">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <p>Valores</p>
                <p className="text-valores txt1">Rentabilidad</p>
                <p className="text-valores">Compromiso</p>
                <p className="text-valores">Calidad</p>
                <p className="text-valores">Transparencia</p>
                <p className="text-valores">Honestidad</p>
                <p className="text-valores">Innovacion</p>
                <p className="text-valores">Trabajo en Equipo</p>
                <p className="text-valores txt8">Respeto</p>
            </div>
        </section>
    )
}